import { observer } from "mobx-react-lite";
import React, { Dispatch, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  ArrowRight,
  CaseLarge,
  Change,
  HomeIcon,
  Mobile,
  PasswordIcon,
  Person,
  VideoCall,
  Warn,
} from "../../../../assets/icons";
import Basket from "../../../../components/Basket/basket";
import Button from "../../../../components/Button/button";
import Card from "../../../../components/Card/card";
import GreyText from "../../../../components/GreyText/greyText";
import IconComp from "../../../../components/IconComp/iconComp";
import InfoComp from "../../../../components/InfoComp/infoComp";
import Text from "../../../../components/Text/text";
import Title from "../../../../components/Title/title";
import { COLORS } from "../../../../constants/colors";
import { APP_ROUTES } from "../../../../router/Route";
import styles from "./main.module.css";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../../store";
// import Check from "../../../../components/Check/Check";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  LogoutForMainAndMarketPage,
  LogoutProject,
} from "../../../../hook/useFaceIdLogin";
import BranchModal from "../../../../components/branchModal/branchModal";
import ModalPassword from "../../../../components/modalPassword/modalPassword";
import { FormControlLabel, Switch } from "@mui/material";
import DisactiveModal from "../../../../components/disactiveModal/disactiveModal";

const MainView = () => {
  const navigation = useNavigate();
  const [branchModal, setBranchModal] = useState<Boolean>(false);
  const [modalPassword, setModalPassword] = useState<Boolean>(false);
  const [disactiveModal, setDisactiveModal] = useState<Boolean>(false);
  const [objectId, setObjectId] = useState<any>("");
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch.profileSlice.getUser();
    dispatch.basketSlice.getAddCard();
    dispatch.OtherSlice.getShops();
    setObjectId(localStorage.getItem("object_id"));
  }, []);

  useEffect(() => {
    setTimeout(() => {
      dispatch.orderSlice.isResultFunction(false);
    }, 5000);
  }, []);

  const { userList } = useSelector((state: RootState) => state.profileSlice);

  const { priceList } = useSelector((state: RootState) => state.basketSlice);

  const isLoading = useSelector(
    (state: RootState) => state.loading.models.profileSlice
  );

  const logout = () => {
    localStorage.removeItem("@token");
    dispatch.profileSlice.logout();
    navigation(APP_ROUTES.BANNER);
  };

  let date = new Date();

  const { toast } = useSelector((state: RootState) => state.orderSlice);

  LogoutForMainAndMarketPage();

  const { shopList } = useSelector((state: RootState) => state.OtherSlice);

  return (
    <>
      <ToastContainer position="top-center" />
      {toast && (
        <div
          className={styles.popup}
          onClick={() => dispatch.orderSlice.isResultFunction(false)}
        >
          <div className={styles.modal}>
            {/* <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="#000000"
              width="200px"
              height="200px"
              viewBox="0 0 14 14"
              role="img"
              focusable="false"
              aria-hidden="true"
            >
              <path
                fill="green"
                d="M13 4.1974q0 .3097-.21677.5265L7.17806 10.329l-1.0529 1.0529q-.21677.2168-.52645.2168-.30968 0-.52645-.2168L4.01935 10.329 1.21677 7.5264Q1 7.3097 1 7t.21677-.5265l1.05291-1.0529q.21677-.2167.52645-.2167.30968 0 .52645.2167l2.27613 2.2839 5.07871-5.0864q.21677-.2168.52645-.2168.30968 0 .52645.2168l1.05291 1.0529Q13 3.8877 13 4.1974z"
              />
            </svg> */}
            <p>   !</p>
            <p>  !</p>
          </div>
        </div>
      )}
      {isLoading ? (
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            height: "100vh",
          }}
        >
          <p style={{ fontSize: "30px" }}>Loading...</p>
        </div>
      ) : (
        <div className={styles.container}>
          <div className={styles.header}>
            <div
              className={styles.headerTextId}
              style={{
                marginRight: "auto",
                display: "flex",
                flexDirection: "column",
              }}
            >
              <div style={{ display: "flex" }}>
                <div className={styles.headerId}>
                  ID: <span className={styles.numberId}>{userList?.id}</span>
                </div>
                <Title title={`${userList.full_name}` || ""} fontSize="28px" />
              </div>
              <div>
                <h2 style={{ fontSize: "28px" }}>
                  {`: ${userList.branch_name}` || ""}
                </h2>
              </div>
            </div>
            <IconComp
              iconType="primary"
              onPress={() => setModalPassword(true)}
              icon={<PasswordIcon />}
              text=""
            />
            <IconComp
              iconType="primary"
              onPress={() => setBranchModal(true)}
              icon={<Change />}
              text=""
            />
            <IconComp
              iconType="primary"
              onPress={() => logout()}
              icon={<ArrowRight />}
              text=""
            />
          </div>
          <div className={styles.info}>
            <InfoComp
              title={`${priceList.balance?.toLocaleString("ru-RU")} `}
              text="  "
              titleColor={COLORS.green}
            />
            <InfoComp
              title={`${priceList.limit_summa?.toLocaleString("ru-RU")}` || ""}
              text="  "
              titleColor={COLORS.orange}
            />
            <InfoComp
              title={`${priceList.limit_summa?.toLocaleString("ru-RU")}` || ""}
              text="  "
              titleColor={COLORS.crimson}
            />
            <InfoComp
              title="10 "
              text="  "
              titleColor={COLORS.blue}
            />
            <InfoComp
              title="10 "
              text="  "
              titleColor={COLORS.darkBlue}
            />
          </div>

          <div className={styles.category}>
            {shopList.map((item) => (
              <Card
                icon={<CaseLarge />}
                text={item.name}
                onPress={() => {
                  navigation(`/${objectId}/market/` + item.id);
                  dispatch.productSlice.getProduct({
                    shop_id: item.id,
                    category_id: 0,
                  });
                }}
              />
            ))}
            <Card icon={<Mobile />} namediv={true} text="" />
            <Card icon={<VideoCall />} namediv={true} text="" />
            <a
              style={{
                textDecoration: "none",
              }}
              href={`#/${objectId}/main`}
              target=""
            >
              <Card icon={<Person />} text="" />
            </a>
            <a
              style={{
                textDecoration: "none",
              }}
              href={`#/${objectId}/main`}
              target=""
            >
              <Card icon={<Warn />} text="" />
            </a>
          </div>

          <div className={styles.footer}>
            <Button
              style={{ width: "350px", marginBottom: "40px" }}
              onPress={() => navigation(APP_ROUTES.TRANSACTION)}
              btnSize="large"
              btnType="primary"
              title=" "
            />
          </div>
          {/* <Check /> */}
        </div>
      )}
      <BranchModal
        open={branchModal}
        onPress={() => setBranchModal(false)}
        handleClose={() => setBranchModal(false)}
      />
      <ModalPassword
        open={modalPassword}
        onPress={() => setModalPassword(false)}
        userInfo={userList}
      />
    </>
  );
};

export default observer(MainView);
